<template>
  <div>
    <div class='verifiedImage'>
        <el-upload
            class='upload-demo'
            action='#'
            ref='upload'
            :show-file-list='false'
            :multiple='true'
            accept='image/*'
            :on-change='handleChange'
            :on-exceed='handleChangeMax'
            :http-request='none'
            :file-list='fileList'
            :auto-upload='true'
            :limit='4'
            list-type='picture-card'>
            <img :src="url?url:require('../../assets/real/positivezheng.png')" class='iDcard'/>
        </el-upload>
    </div>
    <div class='verifiedName'>
        添加身份证正面照
    </div>
    <div class='verifiedImage'>
        <el-upload
            class='upload-demo'
            action='#'
            ref='upload'
            :show-file-list='false'
            :multiple='true'
            accept='image/*'
            :on-change='handleTwo'
            :on-exceed='handleChangeMax'
            :http-request='none'
            :file-list='fileList'
            :auto-upload='true'
            :limit='4'
            list-type='picture-card'>
            <img :src="urlTwo?urlTwo:require('../../assets/real/positivebei.png')" class='iDcard'/>
        </el-upload>
    </div>
    <div class='verifiedName'>
        添加身份证背面照
    </div>
    <div class='verifiedImage'>
        <el-upload
            class='upload-demo'
            action='#'
            ref='upload'
            :show-file-list='false'
            :multiple='true'
            accept='image/*'
            :on-change='handleThree'
            :on-exceed='handleChangeMax'
            :http-request='none'
            :file-list='fileList'
            :auto-upload='true'
            :limit='4'
            list-type='picture-card'>
            <img :src="urlThree?urlThree:require('../../assets/real/cardzheng.png')" class='iDcard'/>
        </el-upload>
    </div>
    <div class='verifiedName'>
        添加银行卡正面照
    </div>
    <div class='verifiedImage'>
        <el-upload
            class='upload-demo'
            action='#'
            ref='upload'
            :show-file-list='false'
            :multiple='true'
            accept='image/*'
            :on-change='handleFour'
            :on-exceed='handleChangeMax'
            :http-request='none'
            :file-list='fileList'
            :auto-upload='true'
            :limit='4'
            list-type='picture-card'>
            <img :src="urlFour?urlFour:require('../../assets/real/cardbei.png')" class='iDcard'/>
        </el-upload>
    </div>
    <div class='verifiedName'>
        添加银行卡背面照
    </div>
    <div class='transferConfirm adduser-mar-top' @click='uploadImg()' element-loading-background="rgba(0, 0, 0, 0.7)" v-loading.fullscreen.lock='fullscreenLoading'>
        下一步
    </div>
    <div class='bottomLong'></div>
  </div>
</template>
<script>
import axios from 'axios'

export default {
  data () {
    return {
      dialogImageUrl: '',
      dialogVisible: false,
      fileList: [],
      fileTwo: [],
      fileThree: [],
      fileFour: [],
      imgUrlBase64: {},
      url: '',
      urlTwo: '',
      urlThree: '',
      urlFour: '',
      card: '',
      name: '',
      fullscreenLoading: false,
      phone: '',
      merchantId: '',
      token: '',
      tokenId: '',
      queryparmas: ''
    }
  },
  created () {
    this.version = this.$stact.state.version
    this.agentNo = this.$stact.state.agentNo
    this.merchantNo = JSON.parse(this.$stact.state.token)[0].merchantNo
    // this.cardName()
  },
  mounted () {
    this.$inputLen.inputJs()
  },
  methods: {
    async handleChange (file, fileList) {
      if (fileList.length > 0) {
        this.fileList = fileList
        this.url = fileList[fileList.length - 1].url
        await this.chageBase('10E', fileList[fileList.length - 1])
      }
      return false
    },
    async handleTwo (file, fileList) {
      if (fileList.length > 0) {
        this.fileTwo = fileList
        this.urlTwo = fileList[fileList.length - 1].url
        await this.chageBase('10F', fileList[fileList.length - 1])
      }
      return false
    },
    async handleThree (file, fileList) {
      if (fileList.length > 0) {
        this.fileThree = fileList
        this.urlThree = fileList[fileList.length - 1].url
        await this.chageBase('10K', fileList[fileList.length - 1])
      }
      return false
    },
    async handleFour (file, fileList) {
      if (fileList.length > 0) {
        this.fileFour = fileList
        this.urlFour = fileList[fileList.length - 1].url
        await this.chageBase('10L', fileList[fileList.length - 1])
      }
      return false
    },
    async handleChangeMax (file, fileList) {
      this.$message({
        message: '上传图片次数过多，请勿重复',
        center: true,
        offset: 30,
        duration: 2500,
        type: 'success'
      })
    },
    none () {
      console.log(1)
    },
    cardName () {
      let vm = this
      let parmas = {
        '0': '0700',
        '3': '190936',
        '21': vm.merchantNo,
        '59': vm.version
      }
      let url = vm.$utils.queryParams(vm.$mdata.mdGet(parmas))
      vm.$http.get(`request.app${url}`)
        .then(res => {
          if (res.data[39] === '00') {
            vm.token = res.data[41]
            vm.tokenId = res.data[42]
            vm.real()
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    real () {
      let vm = this
      let parmas = {
        '0': '0700',
        '3': '190937',
        '21': vm.merchantNo,
        '22': vm.tokenId,
        '59': vm.version
      }
      let url = vm.$utils.queryParams(vm.$mdata.mdGet(parmas))
      vm.$http.get(`request.app${url}`)
        .then(res => {
          if (res.data[39] === '00') {
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    chageBase (type, fileList) {
      let vm = this
      let file = fileList.raw
      // 将文件以Data URL形式读入页面
      let reader = new FileReader()
      this.imgUrlBase64 = reader.readAsDataURL(file)
      reader.onload = function () {
        let url = reader.result.split(',')[1]
        let parmas = {
          'imageData': url,
          'imageType': type,
          'merchantNo': vm.merchantNo
        }
        vm.fullscreenLoading = true
        let config = {
          headers: {
            'Content-Type': 'application/json; charset=utf-8'
          }
        }
        const root = process.env.NODE_ENV === 'production' ? 'http://yihuan.llyzf.cn/lly-posp-proxy/' : '/apis'
        axios.post(root + 'app/imageUpload.app', parmas, config)
          .then(res => {
            vm.fullscreenLoading = false
            vm.$message({
              message: res.data.msg,
              center: true,
              offset: 30,
              duration: 2500,
              type: 'success'
            })
          })
          .catch(err => {
            console.log(err)
            vm.fullscreenLoading = false
          })
      }
    },
    uploadImg () {
      let vm = this
      if (this.fileList.length < 1) {
        this.$message({
          message: '必须上传一张身份证正面照',
          center: true,
          offset: 30,
          duration: 2000,
          type: 'warning'
        })
        return
      }
      if (this.fileTwo.length < 1) {
        this.$message({
          message: '必须上传一张身份证背面照',
          center: true,
          offset: 30,
          duration: 2000,
          type: 'warning'
        })
        return
      }
      if (this.fileThree.length < 1) {
        this.$message({
          message: '必须上传一张银行卡正面',
          center: true,
          offset: 30,
          duration: 2000,
          type: 'warning'
        })
        return
      }
      if (this.fileFour.length < 1) {
        this.$message({
          message: '必须上传一张银行卡背面',
          center: true,
          offset: 30,
          duration: 2000,
          type: 'warning'
        })
        return
      }
      // if (this.card.match(/^[ ]*$/)) {
      //   this.$message({
      //     message: '您的身份证号必须填写',
      //     center: true,
      //     offset: 30,
      //     duration: 2000,
      //     type: 'warning'
      //   })
      //   return
      // }
      // let regIdNo = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
      // if (!regIdNo.test(vm.card)) {
      //   this.$message({
      //     message: '您的身份证号填写有误',
      //     center: true,
      //     offset: 30,
      //     duration: 2000,
      //     type: 'warning'
      //   })
      //   return
      // }
      // if (this.name.match(/^[ ]*$/)) {
      //   this.$message({
      //     message: '您的姓名必须填写',
      //     center: true,
      //     offset: 30,
      //     duration: 2000,
      //     type: 'warning'
      //   })
      //   return
      // }
      // vm.$router.push({ name: 'realcard', query: { card: vm.card, name: vm.name } })
      vm.queryparmas = this.$route.query.list
      let url = vm.$utils.queryParams(vm.$mdata.mdGet(vm.queryparmas))
      vm.fullscreenLoading = true
      vm.$http.get(`request.app${url}`)
        .then(res => {
          vm.fullscreenLoading = false
          if (res.data[39] === '00') {
            vm.$message({
              message: '绑卡',
              center: true,
              offset: 30,
              duration: 2000,
              type: 'success'
            })
            vm.$router.push({ name: 'my' })
          } else {
            vm.$message({
              message: res.data[39],
              center: true,
              offset: 30,
              duration: 2000,
              type: 'success'
            })
          }
        })
        .catch(err => {
          console.log(err)
          vm.fullscreenLoading = false
        })
    }
  }
}
</script>
